/* Global styles */
body {
  font-family: 'Roboto', Arial, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #fbede2;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color:white;
  padding: 1rem;
  font-family: 'Roboto';
}

.nav-links {
  display: flex;
  grid-gap: 2rem;
  gap: 2rem;
  margin-left: 10px;
}

.nav-links a {
  color: black;
  text-decoration: none;
  font-size: 1.2rem;
  position: relative;
  font-family: 'Poppins';
  cursor: pointer; /* Set cursor to pointer */
}

.nav-links a::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  display: block;
  margin-top: 5px;
  right: 0;
  background: black;
  transition: width 0.4s ease;
  -webkit-transition: width 0.4s ease;
}

.nav-links a:hover::after {
  width: 100%;
  left: 0;
  background-color: #79624f;
}

.nav-links a.active::after {
  width: 100%;
  left: 0;
  background-color: #79624f;
}

.navbar-icon img {
  width: 40px;
  height: 40px;
  margin-right: 10px;
}

